  width: 24px;
  border-radius: 100%;
  fill: var(--jp-ui-font-color1);
  color: var(--jp-ui-font-color1);
}

.jp-UserMenu-caretDownIcon {
  height: 22px;
  position: relative;
  top: 15%;
}
