 * Copyright (c) Jupyter Development Team.
 * Distributed under the terms of the Modified BSD License.
 */

/************************************************************
                      Main Panel
*************************************************************/

.jp-RTCPanel {
  min-width: var(--jp-sidebar-min-width) !important;
  color: var(--jp-ui-font-color1);
  background: var(--jp-layout-color1);
  font-size: var(--jp-ui-font-size1);
}

/************************************************************
                      User Info Panel
*************************************************************/
.jp-UserInfoPanel {
  display: flex;
  flex-direction: column;
  max-height: 140px;
  padding-top: 3px;
}

.jp-UserInfo-Container {
  margin: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.jp-UserInfo-Icon {
  margin: auto;
  width: 50px;
  height: 50px;
  border-radius: 50px;
  display: inline-flex;
  align-items: center;
}

.jp-UserInfo-Icon span {
  margin: auto;
  text-align: center;
  font-size: 25px;
  fill: var(--jp-ui-font-color1);
  color: var(--jp-ui-font-color1);
}

.jp-UserInfo-Info {
  margin: 20px;
  display: inline-flex;
  flex-direction: column;
}

.jp-UserInfo-Info label {
  font-weight: bold;
  fill: var(--jp-ui-font-color1);
  color: var(--jp-ui-font-color1);
}

.jp-UserInfo-Info input {
  text-decoration: none;
  border-top: none;
  border-left: none;
  border-right: none;
  border-color: var(--jp-ui-font-color1);
  border-width: 0.5px;
  background-color: transparent;
  fill: var(--jp-ui-font-color1);
  color: var(--jp-ui-font-color1);
}

/************************************************************
                Collaborators Info Panel
*************************************************************/

.jp-CollaboratorsPanel {
  overflow-y: auto;
}

.jp-CollaboratorsList {
  flex-direction: column;
  display: flex;
  z-index: 1000;
}

.jp-CollaboratorHeader {
  padding: 10px;
  display: flex;
  align-items: center;
  font-size: var(--jp-ui-font-size0);
  fill: var(--jp-ui-font-color1);
  color: var(--jp-ui-font-color1);
}

.jp-CollaboratorHeader > span {
  padding-left: 7px;
}

.jp-ClickableCollaborator:hover {
  cursor: pointer;
  background-color: var(--jp-layout-color2);
  fill: var(--jp-ui-font-color0);
  color: var(--jp-ui-font-color0);
}

.jp-CollaboratorHeaderCollapser {
  transform: rotate(-90deg);
  margin: auto 0;
  height: 16px;
}

.jp-CollaboratorHeader:not(.jp-ClickableCollaborator) .jp-CollaboratorHeaderCollapser {
  visibility: hidden;
}

.jp-CollaboratorHeaderCollapser.jp-mod-expanded {
  transform: rotate(0deg);
}

.jp-CollaboratorIcon {
  border-radius: 100%;
  padding: 2px;
  width: 24px;
  height: 24px;
  display: flex;
}

.jp-CollaboratorIcon > span {
  text-align: center;
  margin: auto;
  font-size: 12px;
  fill: var(--jp-ui-font-color1);
  color: var(--jp-ui-font-color1);
}

.jp-CollaboratorFiles {
  padding-left: 1em;
  margin-top: 0;
  box-shadow: 0 2px 2px -2px rgb(0 0 0 / 24%);

}
